import "./styles.css";

const onClickAdd = () => {
  const inputText = document.getElementById("input-text").value;
  document.getElementById("input-text").value = "";

  createImcompleteList(inputText);
};

const createImcompleteList = (text) => {
  // li作成
  const li = document.createElement("li");

  // divの作成
  const div = document.createElement("div");
  div.className = "list-row";

  // pの作成
  const p = document.createElement("p");
  p.innerText = text;
  div.appendChild(p);

  // 完了ボタン作成
  const completeButton = document.createElement("button");
  completeButton.innerText = "完了";
  completeButton.addEventListener("click", () => {
    const target = completeButton.closest("li");
    deleteFromincomplete(target);
    // 内容を取得
    const nodes = target.firstChild.childNodes;
    const content = nodes[0];
    const text = content.innerText;
    // 戻すボタン作成
    const backButton = document.createElement("button");
    backButton.innerText = "戻す";
    backButton.addEventListener("click", () => {
      const completeContent = backButton.closest("li");
      document.getElementById("complete-list").removeChild(completeContent);
      createImcompleteList(text);
    });
    // 完了リストに追加
    target.firstChild.textContent = null;
    target.firstChild.appendChild(content);
    target.firstChild.appendChild(backButton);
    document.getElementById("complete-list").appendChild(target);
  });

  // 削除ボタン作成
  const deleteButton = document.createElement("button");
  deleteButton.innerText = "削除";
  deleteButton.addEventListener("click", () => {
    deleteFromincomplete(deleteButton.closest("li"));
  });

  div.appendChild(p);
  div.appendChild(completeButton);
  div.appendChild(deleteButton);
  li.appendChild(div);

  document.getElementById("incomplete-list").appendChild(li);
};

// 指定のデータを削除
const deleteFromincomplete = (deleteTarget) => {
  document.getElementById("incomplete-list").removeChild(deleteTarget);
};

document
  .getElementById("add-button")
  .addEventListener("click", () => onClickAdd());
